import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";
import { withStyles } from "@material-ui/core/styles";
import states from "../api/states";
import { editExpense, deleteExpense, findExpGroups, findTrucks, } from "../api/api";
import {
  Button,
  ButtonGroup,
  FormGroup,
  Form,
  Label,
  Input,
  Card,
  CardBody,
  CardHeader,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Alert,
} from 'reactstrap';

const defaultToolbarSelectStyles = {
  iconButton: {
  },
  iconContainer: {
    marginRight: "24px",
  },
  inverseIcon: {
    transform: "rotate(90deg)",
  },
};

class CustomToolbarSelectExpenses extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
        showEditModal:false,
        saved:false,
        id: '',
        type : 'nn',
        truck : 'nn',
        amount : '',
        description:'',
        startdate:'',
        enddate:'',
        frequency:'nn',
        limit:'',
        city:'',
        state:'nn',
        misc_amount:'',
        xtoken:'',
        msg: '',
        error: '',
        showmore:true,
        trucks:[],
        groups:[],
    };
  }
  hideEditModal = () => {
    this.setState({
      ...this.state,
      showEditModal:false,
    });
    if(this.state.saved)
    {
      this.props.rerenderParentCallback();
    }
  }

  handleEditTypeInput = (event) => {
        const v = event.target.value;
        if( parseInt(v) === 1)
        {
            this.setState({...this.state, type: v, showmore:true});
        }
        else if( parseInt(v) > 1 )
        {
            this.setState({...this.state, type: v, showmore:false});
        }
    }
    handleEditTruckInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, truck: v,});
    }
    handleEditAmountInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, amount: v,});
    }
    handleEditDescriptionInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, description: v,});
    }
    handleEditStartDateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, startdate: v,});
    }
    handleEditEndDateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, enddate: v,});
    }
    handleEditFrequencyInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, frequency: v,});
    }
    handleEditLimitInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, limit: v,});
    }
    handleEditCityInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, city: v,});
    }
    handleEditStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleEditMscAmountInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, misc_amount: v,});
    }
  handleEditSaveChanges = () => {
      editExpense(this.state.xtoken, this.state, this.state.id)
      .then((response) => {
        //   console.log('save error', response);
          if( response.status === 200)
          {
            this.setState({
              ...this.state,
              saved:true,
              error: '',
              msg: response.message,
            });
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
          });
          console.log('error', error);
      });
  }
  handleClickEditRow = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    findExpGroups(this.props.Xtoken).then((response) => {
        this.setState({
            ...this.state,
            groups:response.data,
        });
    }).catch((respError) => {});

    findTrucks(this.props.Xtoken).then((response) => {
        this.setState({
            ...this.state,
            trucks:response.data,
        });
    }).catch((respError) => {});
    this.setState({
        ...this.state,
        showEditModal:true,
        id: selectedRowData.id,
        type : selectedRowData.type,
        truck : selectedRowData.truck,
        amount : selectedRowData.amount,
        description:selectedRowData.description,
        startdate:selectedRowData.startdate,
        enddate:selectedRowData.enddate,
        frequency:selectedRowData.frequency,
        limit:selectedRowData.limit,
        city:selectedRowData.city,
        state:selectedRowData.state,
        misc_amount:selectedRowData.misc_amount,
        showmore:parseInt(selectedRowData.type) === 1,
        xtoken:this.props.Xtoken,
    });
  }
  handleDeleteSelection = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    deleteExpense(this.props.Xtoken, selectedRowData.id)
    .then((response) => {
      if(response.status === 200 )
      {
        alert(response.message);
        this.props.rerenderParentCallback();
        return;
      }
      alert(response.message);
      return;
    })
    .catch((error) => {
      alert('Connection error occured');
    });
  };

  firstUpper = (str) => {
    let string = str.toLowerCase();
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  handleClickDeselectAll = () => {
    this.props.setSelectedRows([]);
  };

  handleClickBlockSelected = () => {
    console.log(`block users with dataIndexes: ${this.props.selectedRows.data.map(row => row.dataIndex)}`);
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.iconContainer}>

        <Tooltip title={"Edit row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickEditRow}>
            <EditIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Deselect row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickDeselectAll}>
            <IndeterminateCheckBoxIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Delete row"}>
          <IconButton className={classes.iconButton} onClick={this.handleDeleteSelection}>
            <DeleteIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        {/* modals */}
        <Modal
          size="lg"
          isOpen={this.state.showEditModal}
          backdrop={false}>
          <ModalHeader>
            Edit load info
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleSelect">Expense type*</Label>
                        <Input onChange={this.handleEditTypeInput} value={this.state.type} defaultValue={this.state.type} type="select" name="type">
                            <option value="nn" disabled={true}>Select type</option>
                            {
                                this.state.groups.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{stt.name}</option>
                                ))
                            }
                        </Input>
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleSelect">Related truck*</Label>
                        <Input onChange={this.handleEditTruckInput} value={this.state.truck} defaultValue={this.state.truck} type="select" name="truck">
                            <option value="nn" disabled={true}>Select truck</option>
                            {
                                this.state.trucks.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{this.firstUpper(stt.make) + " - " + stt.number}</option>
                                ))
                            }
                        </Input>
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleEmail">Amount($)*</Label>
                        <Input onChange={this.handleEditAmountInput}
                            type="number" 
                            value={this.state.amount}
                            name="amt"
                            placeholder=""
                        />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>

                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="examplePassword">Description*</Label>
                        <Input onChange={this.handleEditDescriptionInput}
                            type="text"
                            value={this.state.description}
                            name="origin"
                            placeholder=""
                        />
                        </FormGroup>
                        {this.state.showmore && (
                            <div>
                                <FormGroup>
                                    <Label for="exampleEmail">Start date*</Label>
                                    <Input onChange={this.handleEditStartDateInput}
                                    type="date" 
                                    value={this.state.startdate}
                                    name="startdate"
                                    placeholder=""
                                    />
                                </FormGroup>
                                <FormGroup>
                                        <Label for="exampleEmail">End date*</Label>
                                        <Input onChange={this.handleEditEndDateInput}
                                        type="date" 
                                        value={this.state.enddate}
                                        name="enddate"
                                        placeholder=""
                                        />
                                </FormGroup>
                            </div>
                        )}
                        { !this.state.showmore && (
                            <div>
                                <FormGroup>
                                    <Label for="exampleNumber">City*</Label>
                                    <Input onChange={this.handleEditCityInput}
                                        type="text"
                                        value={this.state.city}
                                        name="rate"
                                        id="rate"
                                        placeholder=""
                                    />
                                </FormGroup>
                                <FormGroup>
                                    <Label for="exampleSelect">State*</Label>
                                    <Input onChange={this.handleEditStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                                        <option value="nn" disabled={true}>Select state</option>
                                        {
                                            states.data.map((stt, idx) => (
                                                <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                                            ))
                                        }
                                    </Input>
                                </FormGroup>
                            </div>
                        )}
                    </Form>
                    </CardBody>
                </Card>
                </Col>
        
                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        { this.state.showmore && (
                            <div>
                            <FormGroup>
                            <Label for="exampleSelect">Frequency*</Label>
                            <Input onChange={this.handleEditFrequencyInput} value={this.state.frequency} defaultValue={this.state.frequency} type="select" name="freq">
                                <option value="nn" disabled={true}>Select frequency</option>
                                <option value="Weekly">Weekly</option>
                                <option value="Monthily">Monthily</option>
                            </Input>
                            </FormGroup>
                            <FormGroup>
                            <Label for="exampleNumber">Limit*</Label>
                            <Input onChange={this.handleEditLimitInput}
                                type="number"
                                value={this.state.limit}
                                name="limit"
                                id="limit"
                                placeholder=""
                            />
                            </FormGroup>
                            </div>
                        )}
                        { !this.state.showmore && (
                            <FormGroup>
                                <Label for="exampleNumber">Misc. Amount($)*</Label>
                                <Input onChange={this.handleEditMscAmountInput}
                                type="number"
                                value={this.state.misc_amount}
                                name="misc"
                                id="misc"
                                placeholder=""
                                />
                            </FormGroup>
                        )}
                    </Form>
                    </CardBody>
                </Card>
                </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handleEditSaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default withStyles(defaultToolbarSelectStyles, { name: "CustomToolbarSelectExpenses" })(CustomToolbarSelectExpenses);